import { toolsConfigManager } from '../config';

/**
 * GM_XHR成功返回的对象
 * @typedef GMResponse
 * @property {*} response 一定有response属性
 * @property {number} status
 */

/**
 * GM下载函数，一般为GM.xmlHttpRequest
 * @callback GMCallback
 * @param {Object} options 下载选项
 * @returns {Promise<GMResponse>} 响应数据
 */

/**
 * 通过GM API下载来获取数据
 * @param {GMCallback} gmCallback 下载函数
 * @param {string} url 下载链接
 * @returns {Promise<*>} 直接输出数据
 */
const getDataByGM = async (gmCallback, url) => {
  console.debug(`调用GM_XHR, URL:${url}`);
  const response = await gmCallback({
    method: 'get',
    url: url,
    responseType: 'blob',
  });
  console.debug(`响应自${url}:`, response);
  if (response.status !== 200) {
    const errMsg = `GM_XHR 相应错误! 响应码: ${response.status}`;
    console.error(errMsg);
    throw new Error(errMsg);
  }
  return response.response;
};
/**
 * 通过fetch获取二进制数据
 * @param {string} url 链接
 * @returns {Promise<Blob>} 二进制数据
 */
const getDataByFetch = async (url) => {
  console.debug(`调用fetch, URL:${url}`);
  const response = await fetch(url);
  console.debug(`响应自${url}:`, response);
  if (!response.ok) {
    const errMsg = `fetch 响应码错误! 响应码: ${response.status}`;
    console.error(errMsg);
    throw new Error(errMsg);
  }
  const blob = await response.blob();
  return blob;
};
/**
 * 获取二进制数据，可选是否用GM，否则用原生fetch
 * @param {string} url 链接
 * @param {GMCallback} callback GM下载函数，一般为GM.xmlHttpRequest
 * @returns {Promise<*>} 二进制数据
 */
export const getData = async (url, callback = null) => {
  const downloadConfig = toolsConfigManager.config.download;
  let tryNum = downloadConfig.tryNum;
  console.debug(`尝试次数：${tryNum}`);
  let res = null;
  let currentE = null;
  while (tryNum >= 0) {
    try {
      if (callback === null) {
        res = await getDataByFetch(url);
      } else {
        res = await getDataByGM(callback, url);
      }
      return res;
    } catch (e) {
      tryNum--;
      currentE = e;
      console.info(`下载失败，重试（还剩${tryNum}次）：${url}`);
    }
  }
  throw currentE;
};
